@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400;500&family=Jura:wght@400;500&family=Roboto:wght@400;500;700;900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'IBM Plex Mono', monospace;
    font-family: 'Jura', sans-serif;
    font-size: 18px;
}

body {
    background-color: #7f7f7f;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    border: 10px solid #000;
    background: #000;
    width: 320px;
    border-radius: 5px;
}

.keypad {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(70px, auto);
}

input[type="text"] {
    height: 100px;
    width: 275px;
    width: -webkit-fill-available;
    border: 0px;
    background-color: #10111600;
    color: #fff;
    text-align: right;
    font-size: 30px;
    font-weight: 700;
    letter-spacing: 0.25px;
    pointer-events: none;
}

#clear {
    grid-column: 1/3;
    grid-row: 1;
    color: #262834;
}

#result {
    grid-column: 3/5;
    grid-row: 5;
    color: #262834;
}

#backspace {
    color: #262834;
}

button {
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background: #262834;
    color: #fff;
    font-weight: 500;
    font-size: 20px;
    transition: .2s ease-in-out;
}

input:focus,
input:active,
button:focus,
button:active {
    outline: none;
}

.highlight {
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    background: #56cbdb;
}

button:hover {
    filter: opacity(0.75)
}

input:hover {
    filter: opacity(0.1)
}